<template><section>
<h1>Checkbox 多选框</h1>
<p>多选框。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<ul>
<li>在一组可选项中进行多项选择时；</li>
<li>单独使用可以表示两种状态之间的切换，和 <code v-pre="">switch</code> 类似。区别在于切换 <code v-pre="">switch</code> 会直接触发状态改变，而 <code v-pre="">checkbox</code> 一般用于状态标记，需要和提交操作配合。</li>
</ul>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-checkbox&gt;checkbox&lt;/v-checkbox&gt;\n    &lt;v-checkbox v-model=\&quot;checkStatus\&quot; :true-value=\&quot;0\&quot; :false-value=\&quot;1\&quot; @change=\&quot;onChange\&quot;&gt;checkbox&lt;/v-checkbox&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data () {\n            return {\n                checkStatus: 1\n            }\n        },\n        methods: {\n            onChange(value) {\n                console.log(\&quot;checkbox change event!!! \&quot; + value);\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-checkbox>checkbox</v-checkbox>
    <v-checkbox v-model="checkStatus" :true-value="0" :false-value="1" @change="onChange">checkbox</v-checkbox>
</template>


</div>
                  <div slot="description"><h4 id="ji-ben-yong-fa"><a class="header-anchor" href="#ji-ben-yong-fa" aria-hidden="true">¶</a> 基本用法</h4>
<p>简单的 checkbox。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-checkbox</span>&gt;</span>checkbox<span class="hljs-tag">&lt;/<span class="hljs-name">v-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkStatus"</span> <span class="hljs-attr">:true-value</span>=<span class="hljs-string">"0"</span> <span class="hljs-attr">:false-value</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"onChange"</span>&gt;</span>checkbox<span class="hljs-tag">&lt;/<span class="hljs-name">v-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">checkStatus</span>: <span class="hljs-number">1</span>
            }
        },
        <span class="hljs-attr">methods</span>: {
            onChange(value) {
                <span class="hljs-built_in">console</span>.log(<span class="hljs-string">"checkbox change event!!! "</span> + value);
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
                <summary>
