                  <div slot="description"><h4 id="bu-ke-yong"><a class="header-anchor" href="#bu-ke-yong" aria-hidden="true">¶</a> 不可用</h4>
<p>checkbox 不可用。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-checkbox</span> <span class="hljs-attr">disabled</span>&gt;</span>checkbox<span class="hljs-tag">&lt;/<span class="hljs-name">v-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-checkbox</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">disabled</span>&gt;</span>checkbox<span class="hljs-tag">&lt;/<span class="hljs-name">v-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 不可用
  checkbox 不可用。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-checkbox</span> <span class="hljs-attr">disabled</span>&gt;</span>checkbox<span class="hljs-tag">&lt;/<span class="hljs-name">v-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-checkbox</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"true"</span> <span class="hljs-attr">disabled</span>&gt;</span>checkbox<span class="hljs-tag">&lt;/<span class="hljs-name">v-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;div&gt;\n        &lt;p style=\&quot;margin-bottom: 16px;\&quot;&gt;\n            &lt;v-checkbox-group v-model=\&quot;defaultValue\&quot; @change=\&quot;checkGroup\&quot;&gt;\n                &lt;v-checkbox v-for=\&quot;item in options\&quot; :true-value=\&quot;item.value\&quot; :key=\&quot;item.value\&quot;&gt;{{item.label}}&lt;/v-checkbox&gt;\n            &lt;/v-checkbox-group&gt;\n        &lt;/p&gt;\n        &lt;p&gt;\n            &lt;button type=\&quot;button\&quot; class=\&quot;ant-btn ant-btn-primary ant-btn-sm\&quot; @click=\&quot;changeDefaultValue\&quot;&gt;修改默认选中值&lt;/button&gt;\n        &lt;/p&gt;\n        &lt;p style=\&quot;margin-bottom: 16px;\&quot;&gt;\n            &lt;v-checkbox-group @change=\&quot;checkGroup\&quot;&gt;\n                &lt;v-checkbox v-for=\&quot;item in optionsWithDisabled\&quot; :true-value=\&quot;item.value\&quot; :disabled=\&quot;item.disabled\&quot; :key=\&quot;item.value\&quot;&gt;{{item.text}}&lt;/v-checkbox&gt;\n            &lt;/v-checkbox-group&gt;\n        &lt;/p&gt;\n    &lt;/div&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data () {\n            return {\n                defaultValue: ['Apple', 'Orange'],\n                options: [\n                    {label: '苹果', value: 'Apple'},\n                    {label: '梨', value: 'Pear'},\n                    {label: '橘', value: 'Orange'},\n                ],\n                optionsWithDisabled: [\n                    {text: '苹果', value: 'Apple'},\n                    {text: '梨', value: 'Pear'},\n                    {text: '橘', value: 'Orange', disabled: true},\n                ]\n            }\n        },\n        methods: {\n            checkGroup(values) {\n                console.log(\&quot;checkboxGroup change event!!! \&quot;);\n                console.log(values);\n            },\n            changeDefaultValue() {\n                if (this.defaultValue.length &gt; 0) {\n                    this.defaultValue.splice(0,1);\n                } else {\n                    this.defaultValue.push(\&quot;Pear\&quot;);\n                }\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <div>
        <p style="margin-bottom: 16px;">
            <v-checkbox-group v-model="defaultValue" @change="checkGroup">
                <v-checkbox v-for="item in options" :true-value="item.value" :key="item.value">{{item.label}}</v-checkbox>
            </v-checkbox-group>
        </p>
        <p>
            <button type="button" class="ant-btn ant-btn-primary ant-btn-sm" @click="changeDefaultValue">修改默认选中值</button>
        </p>
        <p style="margin-bottom: 16px;">
            <v-checkbox-group @change="checkGroup">
                <v-checkbox v-for="item in optionsWithDisabled" :true-value="item.value" :disabled="item.disabled" :key="item.value">{{item.text}}</v-checkbox>
            </v-checkbox-group>
        </p>
    </div>
</template>


</div>
                  <div slot="description"><h4 id="duo-xuan-kuang-zu"><a class="header-anchor" href="#duo-xuan-kuang-zu" aria-hidden="true">¶</a> 多选框组</h4>
<p>适用于多个勾选框绑定到同一个数组的情景，通过是否勾选来表示这一组选项中选中的项。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
