<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="checkbox-props"><a class="header-anchor" href="#checkbox-props" aria-hidden="true">¶</a> Checkbox Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>指定当前是否选中</td>
<td>Any</td>
<td>false</td>
</tr>
<tr>
<td>true-value</td>
<td>选中时的值</td>
<td>Any</td>
<td>true</td>
</tr>
<tr>
<td>false-value</td>
<td>没有选中时的值</td>
<td>Any</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td>只读，无法进行交互</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>indeterminate</td>
<td>设置 indeterminate 状态，只负责样式控制</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3 id="checkbox-group-props"><a class="header-anchor" href="#checkbox-group-props" aria-hidden="true">¶</a> Checkbox Group Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>默认选中的选项</td>
<td>Array</td>
<td>[]</td>
</tr>
<tr>
<td>keyField</td>
<td>选项的value的字段名</td>
<td>String</td>
<td>value</td>
</tr>
<tr>
<td>label</td>
<td>选项显示的文本的字段名</td>
<td>String</td>
<td>label</td>
</tr>
<tr>
<td>data</td>
<td>选项</td>
<td>Array</td>
<td>—</td>
</tr>
</tbody>
</table>
<h3 id="data-props"><a class="header-anchor" href="#data-props" aria-hidden="true">¶</a> Data Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>选项的值(该字段可通过keyField属性修改)</td>
<td>String</td>
<td>—</td>
</tr>
<tr>
<td>label</td>
<td>选项的标签(可通过label属性修改)</td>
<td>String</td>
<td>—</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3 id="checkbox-events"><a class="header-anchor" href="#checkbox-events" aria-hidden="true">¶</a> Checkbox Events</h3>
<table class="table"><thead>
<tr>
<th>事件</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>值发生变化的时候触发</td>
<td>value</td>
</tr>
<tr>
<td>click</td>
<td>点击的时候触发</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="checkbox-group-events"><a class="header-anchor" href="#checkbox-group-events" aria-hidden="true">¶</a> Checkbox Group Events</h3>
<table class="table"><thead>
<tr>
<th>事件</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>选择的值发生变化的时候触发</td>
<td>value</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data() {
        return {
            checkStatus: 1,
            checked: false,
            disabled: false,
            defaultValue: ['Apple', 'Orange'],
            arrayValue: ['Apple', 'Orange'],
            options: [
                {label: '苹果', value: 'Apple'},
                {label: '梨', value: 'Pear'},
                {label: '橘', value: 'Orange'},
            ],
            optionsWithDisabled: [
                {text: '苹果', value: 'Apple'},
                {text: '梨', value: 'Pear'},
                {text: '橘', value: 'Orange', disabled: true},
            ],
            indeterminate: true,
            allChecked: false,
            fruits: ['Apple', 'Orange'],
            allFruits: ['Apple', 'Pear', 'Orange']
        }
    },
    methods: {
        onChange(value) {
            console.log("checkbox change event!!! " + value);
        },
        checkGroup(values) {
            console.log("checkboxGroup change event!!! ");
            console.log(values);
        },
        changeArrayValue() {
            if (this.arrayValue.length > 0) {
                this.arrayValue.splice(0,1);
            } else {
                this.arrayValue.push("Pear");
            }
        },
        changeDefaultValue() {
            if (this.defaultValue.length > 0) {
                this.defaultValue.splice(0,1);
            } else {
                this.defaultValue.push("Pear");
            }
        },
        checkAll() {
            if (this.fruits.length == this.options.length) {
                this.fruits = [];
                this.allChecked = false;
                this.indeterminate = false;
            } else {
                this.fruits = JSON.parse(JSON.stringify(this.allFruits));
                this.allChecked = true;
                this.indeterminate = false;
            }
        },
        setState() {
            this.indeterminate =  this.fruits.length > 0 && this.fruits.length < this.options.length;
            this.allChecked = this.fruits.length == this.options.length;
        }
    }
}
</script>


